.header {
    &__main {
    background-color: #003149;
    display: flex;
    justify-content: space-between;
    line-height: 84px;
    height: 84px;
    }

    &__burger {
        position: fixed;
        svg path{
            fill: #87d4fb;
        }
        margin: 0 15px;
    }
}



.header-theme, .header__user {
    svg path{
        fill: #87d4fb;
    }
}

.header-theme {
    margin-left: 100px;
}

.button--pokeball {
    height: 100%;
    width: 70px;
    & * {
    position: fixed;
    top: 8px;
    right: 10px;
    height: 70px;
    padding: 0 10px;
    }
}

.logo * {
    cursor: pointer;
    color: red;
    svg path{
        fill: #87d4fb;
    }
}

.header__user {
    & svg {
        height: 60px;
        vertical-align: middle;
    }
}


.header__logo {
    
    & svg {
        width: 200px;
    }
    svg path{
        fill: #87d4fb;
        width: 150px;
        height: 150px;
    }
}

@media screen and (max-width: 640px) {
    .header__logo {
        
        & svg {
            width: 150px;

        }
        right: 15px;
        svg path{
            fill: #87d4fb;
            
        }
    }

    .header-theme{
        margin-left: 50px;
    }

    .header__user *{
        width: 43px;
    }
    .button--pokeball * {
        height: 50px;
        top: 18px;
    }
}

.pokeball {
    margin-left: 10px;
}


@media screen and (max-width: 400px) {
    .header__logo {
        
        & svg {
            width: 100px;

        }
        right: 15px;
        svg path{
            fill: #87d4fb;
            
        }
    }
}